/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"
import "./layout.css"

import { rhythm } from "../utils/typography"

const Bio = () => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
        childImageSharp {
          fixed(width: 50, height: 50) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author
        }
      }
    }
  `)

  const author  = data.site.siteMetadata
  return (
    <div
      style={{
        display: `flex`,
        marginBottom: rhythm(0.5),
      }}
    >
      <Image
        fixed={data.avatar.childImageSharp.fixed}
        alt={author}
        style={{
          marginRight: rhythm(1 / 2),
          marginBottom: 0,
          minWidth: 50,
          borderRadius: `100%`,
        }}
        imgStyle={{
          borderRadius: `50%`,
        }}
      />
      <p>
        <small>セキュリティ, エクスプロイト, Tor, 仮想化とか　-　written by "_spxn"</small><br/>{`　`}
        <a href={`https://twitter.com/_spxn`}><i className="fa fa-twitter"></i></a>{`　`}
        <a href={`https://twitter.com/_superconsole`}><i className="fa fa-twitter"></i>'</a>{`　`}
        <a href={`https://github.com/SuperConsole`}><i className="fa fa-github"></i></a>{`　`}
        <a href={`https://soundcloud.com/SuperConsole`}><i className="fa fa-soundcloud"></i></a>{`　`}
        <a href={`https://www.youtube.com/channel/UCtfFkG9H7xVMiPCcmGdpN_A/videos`}><i className="fa fa-youtube"></i></a>{`　`}
      </p>
    </div>
  )
}

export default Bio
